  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* My Css */
:root {
  --snow-background-color: white;
  --snow-font-color: black;
  --partially-cloudy-background-color: navy;
  --partially-cloudy-font-color: white;
  --overcast-background-color: grey;
  --overcast-font-color: black;
  --clear-background-color: skyblue;
  --clear-font-color: white;
  --rain-background-color: black;
  --rain-font-color: white;
}

body {
  height: 100vh;
  display: grid;
  grid-template-rows: minmax(50px, 1.5fr) minmax(500px, 85vh) minmax(30px, 1fr);
}

header,
footer {
  display: grid;
  place-items: center;
  font-weight: bold;
}

header {
  font-size: 2rem;
  grid-template-columns: minmax(100px, 10vw) minmax(300px, 1fr) minmax(100px, 10vw);
}

.heading {
  grid-column: 2;
}

.degree-buttons {
  display: flex;
  height: 50%;
  font-size: 1rem;
  align-items: center;
  font-weight: normal;
}

.degree-button {
  appearance: none;
  border: none;
}

.degree-button:hover {
  background-color: grey;
  border: 1px solid grey;
  font-weight: bold;
}

.container {
  opacity: 0.8;
  display: grid;
  place-content: center;
  grid-template: auto minmax(250px, 25vh) / minmax(325px, 20vw);
}

.container * {
  opacity: 1;
}

.content {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 5px;
  padding: 1em 2em 2em;
  display: grid;
  grid-template-areas:
    "address address"
    "logo temperature"
    "condition condition"
    "prediction prediction";
  box-shadow: 2px 2px 5px black;
  grid-template-columns: minmax(20px, 1fr) minmax(30px, 3fr);
  grid-template-rows: minmax(30px, 1fr) minmax(50px, 2fr) repeat(2, minmax(15px, 1fr));
  align-items: center;
}

.address {
  grid-area: address;
}

.logo-container {
  grid-area: logo;
}

.temperature-container {
  font-size: 2rem;
  grid-area: temperature;
  justify-self: end;
  display: flex;
  align-items: center;
  gap: 0.2em;
  font-weight: bold;
}

.current-condition {
  grid-area: condition;
}

.average-condition {
  grid-area: prediction;
}

form {
  display: flex;
  justify-content: space-between;
  padding: 1em;
  border-radius: 5px;
  box-shadow: 2px -1px 5px black;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;;;CAGC;;AAED;;;EAGE,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;;;;;EAKE,cAAc;EACd,eAAe;AACjB;;AAEA;;;;EAIE,aAAa;AACf;;AAEA;;;;;;;EAOE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;AACA;;;;;;EAME,kBAAkB;AACpB;;AAEA;;EAEE,kBAAkB;AACpB;;AAEA,WAAW;AACX;EACE,8BAA8B;EAC9B,wBAAwB;EACxB,yCAAyC;EACzC,oCAAoC;EACpC,iCAAiC;EACjC,4BAA4B;EAC5B,iCAAiC;EACjC,yBAAyB;EACzB,8BAA8B;EAC9B,wBAAwB;AAC1B;;AAEA;EACE,aAAa;EACb,aAAa;EACb,6EAA6E;AAC/E;;AAEA;;EAEE,aAAa;EACb,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,eAAe;EACf,iFAAiF;AACnF;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,WAAW;EACX,eAAe;EACf,mBAAmB;EACnB,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,sBAAsB;EACtB,sBAAsB;EACtB,iBAAiB;AACnB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,qBAAqB;EACrB,6DAA6D;AAC/D;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,0CAA0C;EAC1C,kBAAkB;EAClB,oBAAoB;EACpB,aAAa;EACb;;;;2BAIyB;EACzB,6BAA6B;EAC7B,0DAA0D;EAC1D,oFAAoF;EACpF,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,eAAe;EACf,sBAAsB;EACtB,iBAAiB;EACjB,aAAa;EACb,mBAAmB;EACnB,UAAU;EACV,iBAAiB;AACnB;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,YAAY;EACZ,kBAAkB;EAClB,8BAA8B;AAChC","sourcesContent":["/*\n  Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\np {\n  text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n#root,\n#__next {\n  isolation: isolate;\n}\n\n/* My Css */\n:root {\n  --snow-background-color: white;\n  --snow-font-color: black;\n  --partially-cloudy-background